<template>
    <div class="router-content">
     	<content-head-top class="router-content-header"></content-head-top>
        <el-row class="router-content-body">
			<el-row style="padding:10px;display:flex;">
				<el-input size="medium" style="width: 300px;font-size:12px;flex: 0 0 25%;" placeholder="请输入医院名称/管理员/地区" v-model="input5" class="input-with-select">    
					<el-button slot="append" icon="el-icon-search" @click.native="searchSites"></el-button>
				</el-input>

				<el-select
					v-model="value11"
					multiple
					collapse-tags
					size="medium"
					style="margin-left: 20px; width:150px;flex: 0 0 10%;"
					placeholder="归属地区">
					<el-option
						v-for="item in options"
						:key="item.value"
						:label="item.label"
						:value="item.value">
					</el-option>
				</el-select>

				<el-breadcrumb-item style="flex: 0 0 40%"></el-breadcrumb-item>
			

			
				<el-button style="flex: 0 0 8%">导出</el-button>
				<el-button type="success" style="flex: 0 0 8%" @click.native="addSite">新增</el-button>

			</el-row>
				
			<div style="margin: 10px;">
				<el-table
					:data="siteList"
					border
					max-height="400">
					<el-table-column
					fixed
					prop="site_name"
					label="网点名称"
					width="250">
					</el-table-column>
					<el-table-column
					prop="site_name"
					label="医院名称"
					width="250">
					</el-table-column>
					<el-table-column
					prop="administrator"
					label="管理员"
					width="120">
					</el-table-column>
					<el-table-column
					prop="district"
					label="医院地区"
					width="200">
					</el-table-column>
					<el-table-column
					prop="detailed_address"
					label="详细地址"
					width="400">
					</el-table-column>					
					<el-table-column
					fixed="right"
					label="操作"
					width="180">
					<template slot-scope="scope">
						<el-button @click.native.prevent="putTopRow(scope.$index, scope.row)" type="text" size="small">置顶</el-button>
						<el-button @click.native.prevent="modifyRow(scope.$index, scope.row)" type="text" size="small">修改</el-button>						
						<el-button @click.native.prevent="deleteRow(scope.$index, scope.row)" type="text" size="small">删除</el-button>
					</template>
					</el-table-column>
				</el-table>
			</div>	
  			
  		</el-row>
			<el-dialog
				title="删除"
				:visible.sync="siteDelete.dialogVisible"
				width="30%"
				center>
				<span style="text-align">确定删除该网点吗？</span>
				<span slot="footer" class="dialog-footer">
					<el-button type="primary" size="mini" @click="deleteSiteAccount">确 定</el-button>
				</span>
		  </el-dialog>
    </div>
</template>

<script>
	import ContentHeadTop from '../components/ContentHeadTop'
    export default {
    	components: {
    		ContentHeadTop,
		},
		created() {
			this.searchSites();
		},
		methods: {
      modifyRow(index, row) {
        console.log(row);
				sessionStorage.setItem('site_detail',JSON.stringify(row));
				this.$router.push('addSite');
			},
			putTopRow(index, row) {
				this.$http({
					method: 'POST',
					url: '/hugongljb/adjust_site_to_top',
					data: {
						sid: row['sid'],
						prefered_prio: parseInt(this.prefered_prio_maximal) + 1,
          }
                    
        }).then((response) => {
						console.log(response);
						if(response.status == 200) {
							this.searchSites();
						}
					  console.log(response.status);
				});
			},
			deleteRow(index, row) {
        console.log(row);
				this.siteDelete.dialogVisible = true;
				this.siteDelete.index = index;
				this.siteDelete.rowInfo = row;
			},

			deleteSiteAccount() {
				this.siteDelete.dialogVisible = false;						
				var rowInfo = this.siteDelete.rowInfo;
				console.log(rowInfo['sid']);

				this.$http({
                    method: 'POST',
                    url: '/hugongljb/delete_site_account',
                    data: {
                        sid: rowInfo['sid'],
                    }
                    
                }).then((response) => {
					  console.log(response);
					  console.log(response.status);
					  if(response.status == 200) {
						  this.siteList.splice(this.siteDelete.index, 1);
					  }
				});
			},
			addSite() {
				sessionStorage.setItem('site_detail', null);
				this.$router.push('addSite');
			},
			findPreferedPrioMaximal(sitelist) {
				var prio_max = 0;
				for(let item of this.siteList) {
					if(prio_max < item.prefered_prio) {
						prio_max = item.prefered_prio
					}
					console.log("prio_max = " + prio_max);
				}
				this.prefered_prio_maximal = prio_max;

			},
			async searchSites() {
				this.$http.get("/hugongljb/get_sites")
				.then((response) => {
					  console.log(response);
					this.siteList = response.data.data.list;
					console.log(this.siteList);

					this.findPreferedPrioMaximal(this.siteList);
				})
			}
    },
    data() {
      return {
				siteList:[],
        tableData4: [{
          siteName: '北京友谊医院管理站',
          hospitalName: '上海静安医院',
          administrator: '王小虎',
          region: '普陀区',
          detailedAddress: '上海市普陀区金沙江路 1518 弄',
        }, {
          siteName: '北京友谊医院管理站',
          hospitalName: '上海静安医院',
          administrator: '王小虎',
          region: '普陀区',
          detailedAddress: '上海市普陀区金沙江路 1518 弄',
        }, {
          siteName: '北京友谊医院管理站',
          hospitalName: '上海静安医院',
          administrator: '王小虎',
          region: '普陀区',
          detailedAddress: '上海市普陀区金沙江路 1518 弄',
        }, {
          siteName: '北京友谊医院管理站',
          hospitalName: '上海静安医院',
          administrator: '王小虎',
          region: '普陀区',
          detailedAddress: '上海市普陀区金沙江路 1518 弄',
        }, {
          siteName: '北京友谊医院管理站',
          hospitalName: '上海静安医院',
          administrator: '王小虎',
          region: '普陀区',
          detailedAddress: '上海市普陀区金沙江路 1518 弄',
        }, {
          siteName: '北京友谊医院管理站',
          hospitalName: '上海静安医院',
          administrator: '王小虎',
          region: '普陀区',
          detailedAddress: '上海市普陀区金沙江路 1518 弄',
        }, {
          siteName: '北京友谊医院管理站',
          hospitalName: '上海静安医院',
          administrator: '王小虎',
          region: '普陀区',
          detailedAddress: '上海市普陀区金沙江路 1518 弄',
				}],
				
				siteDelete:{
					rowInfo:'',
					index:'',
					dialogVisible:false,
				},
				prefered_prio_maximal:0,
      }
    }
    }
</script>


<style lang="less">
	@import '../style/mixin';

	.el-table th, .el-table td{
		text-align:center;
	}

</style>



